.yosemite {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

/* top */
.yosemite__top {
  position: absolute;
  left: 50%;
  top: 15%;
  transform: translate(-50%, 0%);
  width: 900px;
  text-align: center;
  transition: top 1s;

  &-title-text {
    stroke: "none";
    font-size: 140px;
    line-height: 150px;
  }


  &-title-descryption {
    font-size: 18px;
    margin: 0;
    padding: 0;
    font-weight: 500;
    letter-spacing: 8px;
    opacity: 0.6;
  }

  &-bg {
    y: 0;
    height: 120px;
    width: 100%;
    transition: y 1s;
  }

  &.hide {
    top: 20%;
    .yosemite__top-bg {
      y: -40;
    }
  }

}
/* top end*/

/* bottom */
.yosemite__bottom {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute !important;
  top: 50%;
  left: 50%;
  transform: translate( -50%, -50%);
  opacity: 1;
  transition: top 0.5s linear;
  width: 800px;

  &-title-name {
    font-size: 120px;
    transition: opacity 0.5s linear;
  }
  &-title-coordinate {
    font-size: 24px;
    letter-spacing: 4px;
    margin-bottom: 10px;
  }
  &-text {
    font-size: 22px;
  }
  &-text, &-title-coordinate {
    line-height: 28px;
    transition: line-height 0.5s linear, opacity 0.5s linear;
    opacity: 1;
  }
  &.hide {
    top: 100%;
    .yosemite__bottom-title-name {
      opacity: 0;
    }
    .yosemite__bottom-text, .yosemite__bottom-title-coordinate {
      line-height: 60px;
      opacity: 0;
    }
  }
}
/* bottom end*/

/* button */
.yosemite__button {
  font-size: 25px;
  line-height: 80px;
  letter-spacing: 5px;
  color: white;
  display: flex;
  align-items: baseline;
  padding: 0 20px 0 25px;
  background: rgba(black, 0.5);
  overflow: hidden;
  border-radius: 50px;
  cursor: pointer;

  &-icon {
    margin-right: 10px;
  }


}

.yosemite__button-top {
  position: absolute;
  left: 50%;
  bottom: -200px;
  transform: translate(-50%,-50%);
  transition: bottom 1s linear;
  &.active {
    bottom: 50px;
  }
}
.yosemite__button-bottom {
  margin-top: 10px;
  transition: opacity 0.5s;
  opacity: 0;
  &.active {
    opacity: 1;
  }
}
/* button end */


.yosemite__container {
  position: relative;
  max-width: 1170px;
  margin: 0 auto;
  text-align: center;
}

.yosemite__background {
  &_back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../images/backgrounds/back.jpg');
    background-position: 50% 0;
    background-repeat: no-repeat;
  }

  &_front {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../images/backgrounds/front.png');
    background-position: 50% 50vh;
    background-repeat: no-repeat;
    background-size: 2000px;
  }
  &_front, &_back {
    transition: background-position 1s, background-size 1s;
  }
  &_back.zoom {
    background-position: 50% -50vh;
  }

  &_front.zoom {
    background-position: 50% -10vh;
    background-size: 3000px;
  }
}

.yosemite__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #000;
  opacity: 0;
  transition: opacity 1.5s;
  &.active {
    opacity: 0.5;
  }
}


/* z-index */
.yosemite__overlay {
  z-index: 2;
}
.yosemite__background_back {
  z-index: 0;
}
.yosemite__background_front {
  z-index: 2;
}
.yosemite__top {
  z-index: 1;
}
.yosemite__bottom {
  z-index: 3;
}
.yosemite__button {
  z-index: 999;
}
/* z-index end */

@media screen and (max-width: 920px) {
  .yosemite__top {
    width: 600px;
  }
  .yosemite__bottom {
    width: 600px;
    &-title-name {
      font-size: 80px;
    }
    &-title-coordinate {
      font-size: 18px;
    }
  }
  .yosemite__button {
    font-size: 14px;
    letter-spacing: 1px;
    line-height: 40px;
  }
}

@media screen and (max-width: 720px) {
  .yosemite__top {
    width: 500px;
  }
  .yosemite__bottom {
    width: 500px;
    &-title-name {
      font-size: 45px;
    }
    &-title-coordinate {
      font-size: 16px;
      letter-spacing: 1px;
      line-height: 1;
    }
    &-text {
      font-size: 15px;
      line-height: 20px;
    }
    &.hide {
      top: 100%;
      .yosemite__bottom-title-name {
        opacity: 0;
      }
      .yosemite__bottom-text, .yosemite__bottom-title-coordinate {
        line-height: 30px;
        opacity: 0;
      }
    }
  }
}

@media screen and (max-width: 510px) {
  .yosemite__top {
    width: 400px;
  }
  .yosemite__bottom {
    width: 400px;
  }
  .yosemite__button {
    font-size: 12px;
  }
}

@media screen and (max-width: 410px) {
  .yosemite__top {
    width: 300px;
    &-title-descryption {
      font-size: 12px;
      padding-left: 10px;
    }
  }
  .yosemite__button {
    font-size: 10px;
  }
  .yosemite__bottom {
    width: 300px;
  }
}
